/* Ocultar scrollbar en navegadores WebKit (Chrome, Safari, etc.) */
.overflow-x-auto::-webkit-scrollbar {
    display: none;
}

/* Ocultar scrollbar en Firefox */
.overflow-x-auto {
    -ms-overflow-style: none;  /* IE y Edge */
    scrollbar-width: none;     /* Firefox */
}

.input-with-button {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .input-with-button input {
    padding-right: 50px; /* Deja espacio para el botón dentro del input */
    flex: 1; /* El input ocupa todo el espacio disponible */
    height: 40px;
  }
  
  .input-with-button button {
    position: absolute;
    right: 5px; /* Ajusta según el espacio que desees entre el botón y el borde del input */
    height: 30px;
    padding: 0 10px; /* Ajusta el padding según el tamaño del botón */
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .input-with-button button:hover {
    background-color: #0056b3; /* Color de fondo del botón al pasar el mouse */
  }
  